import React, {Component} from 'react'
import moment from 'moment'

class Kenoland extends Component{

	constructor(){
	    super();
	    this.state = {
	      time: "4 : 00",
	      days: "",
	      hours: "",
	      minutes: "",
	      seconds: ""
	    }
	    let days, hours, minutes, seconds, endDate
  
  		
	}

	componentDidMount(){
		let now =new Date()
		let returned_endate = moment(now).add(4, 'minutes')
		this.countdown(returned_endate)
	}

 	countdown(endDate) {  		
	  	this.endDate = new Date(endDate).getTime()
			  if (isNaN(endDate)) {
				return;
			  }
		setInterval(this.calculate(), 1000);
	}
  
   calculate() {
    let startDate = new Date();
    startDate = startDate.getTime();
    
    let timeRemaining = parseInt((this.endDate - startDate) / 1000);
    
    if (timeRemaining >= 0) {
      this.days = parseInt(timeRemaining / 86400);
      timeRemaining = (timeRemaining % 86400);
      
      this.hours = parseInt(timeRemaining / 3600);
      timeRemaining = (timeRemaining % 3600);
      
      this.minutes = parseInt(timeRemaining / 60);
      timeRemaining = (timeRemaining % 60);
      
      this.seconds = parseInt(timeRemaining);
       this.setState({
       	time: this.minutes + " : "+ this.seconds
       })
    } else {
      return;
    }
  }

	

	render(){
		return(
			<div className="lotto-card kenoland">
				<div className="img header">
					<div className="head-text">
						<p className="title">
						KENOLAND								
						</p>
						<p className="sub-title">
							Draws every 4 minutes
						</p>
					</div>
				</div>
				<div className="body">
					<p>Next availble draw: <i className="icon icon-clock-2 selection-left"></i> {this.state.time}</p>
					<img src="/static/images/kenolandball.png" />
					<div className="floatLeft"><p>$<span className="large">10</span> million</p></div>
				</div>
			</div>
		)
	}

}

export default Kenoland;